.card-info {
  position: absolute;
  width: 700px;
  min-height: 829px;
  font-size: 25px;
  text-transform: uppercase;
  color: var(--basic-white);
  top: 160px;
  left: 97px;

  @media (max-width: 1600px) {
    width: 558px;
    font-size: 20px;
    min-height: 663px;
    top: 130px;
    left: 78px;
  }

  @media (max-width: 1280px) {
    width: 36.25vw;
    min-height: 43.13vw;
    font-size: 17px;
    top: 107px;
    left: 63px;
  }

  @media (max-width: 1024px) {
    width: 49vw;
    min-height: 58vw;
    font-size: 18px;
    top: 60px;
    left: 21px;
  }
}

.card-info--red {
  background-color: rgba(197, 47, 67, 0.8);
}

.card-info--blue {
  background-color: rgba(21, 106, 158, 0.8);
}

.card-info--orange {
  background-color: rgba(235, 130, 39, 0.8);
}

.card-info--lilac {
  background-color: rgba(127, 126, 172, 0.8);
}

.card-info h2 {
  font-size: 59px;
  line-height: 60px;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 47px;
    line-height: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 39px;
    line-height: 40px;
  }

  @media (max-width: 1024px) {
    font-size: 42px;
    line-height: 45px;
  }
}

.card-info__wrap {
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  margin-top: 107px;

  @media (max-width: 1600px) {
    margin-top: 85px;
  }

  @media (max-width: 1280px) {
    margin-top: 72px;
  }

  @media (max-width: 1024px) {
    margin-top: 39px;
  }
}

.card-info__text {
  padding-left: 38px;

  @media (max-width: 1600px) {
    padding-left: 30px;
  }

  @media (max-width: 1280px) {

  }
}

.card-info__text p {
  position: relative;
  line-height: 27px;
  margin: 0;
  margin-bottom: 157px;

  @media (max-width: 1600px) {
    line-height: 21px;
    margin-bottom: 125px;
  }

  @media (max-width: 1280px) {
    line-height: 18px;
    margin-bottom: 103px;
  }
}

.card-info__text p:last-child {
  margin-bottom: 0;
}

.card-info__img {
  position: relative;
  width: 230px;
  height: 230px;

  @media (max-width: 1600px) {
    width: 185px;
    height: 185px;
  }

  @media (max-width: 1280px) {
    width: 11.95vw;
    height: 11.95vw;
  }

  @media (max-width: 1024px) {
    width: 16.11vw;
    height: 16.11vw;
  }
}

.card-info__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.card-info__wrap a {
  display: block;
  color: var(--basic-white);
  margin-top: 35px;

  @media (max-width: 1600px) {
    margin-top: 27px;
  }

  @media (max-width: 1280px) {
    margin-top: 23px;
  }
}
