.card {
  position: relative;
}

.card::after {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  content: "";
  width: 115px;
  height: 115px;
  font-family: inherit;
  font-size: 21px;
  text-transform: uppercase;
  letter-spacing: -0.5px;
  text-align: center;
  color: var(--basic-white);
  border-radius: 50%;
  padding-left: 20px;
  padding-right: 20px;
  z-index: 20;

  @media (max-width: 1600px) {
    width: 90px;
    height: 90px;
    font-size: 19px;
    padding-left: 10px;
    padding-right: 10px;
}

  @media (max-width: 1280px) {
    width: 76px;
    height: 76px;
    font-size: 16px;
  }

  @media (max-width: 1024px) {
    width: 90px;
    height: 90px;
    font-size: 19px;
  }
}

.card--new::after {
  content: "новинка";
  background-color: var(--basic-blue);
}

.card--exclusive::after {
  content: "эксклюзив";
  background-color: var(--back-red);
}

.card--hit::after {
  content: "хит продаж";
  background-color: var(--basic-orange);
}
