.logo {
  position: relative;
  display: grid;
  width: 313px;
  height: 72px;
  cursor: pointer;
  justify-self: center;

  @media (max-width: 1600px) {
    width: 250px;
    height: 55px;
  }

  @media (max-width: 1280px) {
    width: 209px;
    height: 48px;
  }

  @media (max-width: 1024px) {
    width: 239px;
    height: 57px;
    margin-left: 0;
  }
}

.logo img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
