.wrapper {
  max-width: 1920px;
  padding-left: 160px;
  padding-right: 160px;
  margin: 0 auto;

  @media (max-width: 1600px) {
    padding-left: 130px;
    padding-right: 130px;
  }

  @media (max-width: 1280px) {
    padding-left: 108px;
    padding-right: 108px;
  }

  @media (max-width: 1024px) {
    padding-left: 65px;
    padding-right: 65px;
  }
}
