.main-nav {
  position: relative;
}

.main-nav--closed {

}

.main-nav__wrap {

}

.main-nav__list {
  position: absolute;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 8.4vw;
  background-color: var(--basic-white);
  z-index: 10;
}

.main-nav__list.container {
  padding-left: 275px;
  margin-left: 0;

  @media (max-width: 1600px) {
    padding-left: 215px;
  }

  @media (max-width: 1280px) {
    padding-left: 176px;
  }

  @media (max-width: 1024px) {
    padding-left: 72px;
  }
}

.main-nav__list a {
  text-decoration: none;
}


/*  Сворачивание Меню  */

.main-nav__toggle {
  position: absolute;
  top: -75px;
  left: 207px;
  width: 28px;
  height: 16px;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 15;

  @media (max-width: 1600px) {
    width: 22px;
    height: 12px;
    top: -65px;
    left: 160px;
  }

  @media (max-width: 1280px) {
    width: 20px;
    height: 10px;
    top: -60px;
    left: 130px;
  }

  @media (max-width: 1024px) {
    width: 28px;
    height: 14px;
    top: -67px;
    left: 21px;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 2px;
  background-color: var(--back-main);
  box-shadow: 0 8px 0 0 var(--back-main), 0 16px 0 0 var(--back-main);

  @media (max-width: 1600px) {
    width: 22px;
    box-shadow: 0 6px 0 0 var(--back-main), 0 12px 0 0 var(--back-main);
  }

  @media (max-width: 1280px) {
    width: 20px;
    height: 1px;
    box-shadow: 0 5px 0 0 var(--back-main), 0 10px 0 0 var(--back-main);
  }

  @media (max-width: 1024px) {
    width: 28px;
    height: 2px;
    box-shadow: 0 7px 0 0 var(--back-main), 0 14px 0 0 var(--back-main);
  }
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  opacity: 0.5;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.2;
}

.main-nav--opened .main-nav__toggle {
  width: 28px;
  height: 28px;
  top: -83px;
  background-color: transparent;

  @media (max-width: 1600px) {
    width: 22px;
    height: 22px;
    top: -72px;
  }

  @media (max-width: 1280px) {
    width: 20px;
    top: -65px;
  }

  @media (max-width: 1024px) {
    width: 28px;
    height: 28px;
    top: -73px;
  }
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 28px;
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background-color: var(--back-main);

  @media (max-width: 1600px) {
    width: 22px;
  }

  @media (max-width: 1280px) {
    width: 20px;
    height: 1px;
  }

  @media (max-width: 1024px) {
    width: 28px;
    height: 2px;
  }
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  opacity: 0.5;
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.2;
}

.main-nav--closed .main-nav__list li {
  display: none;
}

.main-nav--opened .main-nav__list {
  padding-top: 42px;
  padding-bottom: 42px;

  @media (max-width: 1600px) {
    padding-top: 35px;
    padding-bottom: 30px;
  }

  @media (max-width: 1280px) {
    padding-top: 30px;
    padding-bottom: 25px;
  }

  @media (max-width: 1024px) {
    padding-top: 25px;
    padding-bottom: 20px;
  }
}
